import express from 'express';
import bodyParser from 'body-parser';
import expressGraphQL from 'express-graphql';
import {GraphQLSchema} from 'graphql';
import {GraphQLBooksQuery} from "./GraphQLBooksQuery";
import {ServiceData, ServiceType} from "./ServiceData";

const booksQuery = new GraphQLBooksQuery();
const port = process.env.PORT || 8080;
const serviceName = process.env.SERVICE_NAME || "book-service";
const app = express();

app.use(bodyParser.json());

const schema = new GraphQLSchema({
    query: booksQuery.query
});


app.use('/api/v1/books', expressGraphQL({
    schema: schema,
    graphiql: true
}));


const serviceData = new ServiceData();

app.get('/api/v1/healthcheck/books', async function  (req, res) {
    let books = await serviceData.fetch(ServiceType.BOOK_SERVICE);
    res.json(books);
});

app.get('/api/v1/healthcheck/authors', async function  (req, res) {
    let authors = await serviceData.fetch(ServiceType.AUTHOR_SERVICE);
    res.json(authors);
});

app.get('/api/v1/healthcheck/inventory', async function  (req, res) {
    let inventory = await serviceData.fetch(ServiceType.INVENTORY_SERVICE);
    res.json(inventory);
});


app.listen(port, () => {
    console.log(`${serviceName} listening on port ${port}`);
});



